/*Error Styles*/

.page-error-container {
  padding: 30px 0;
  position: relative;
  height: 100%;
  @include display-flex(flex, column, nowrap);
  @include justify-content(center);

  @media screen and (max-width: 575px) {
    padding: 15px 0;
  }
}

.page-error-content {
  margin: 0 auto;
  width: 380px;
  max-width: 94%;
}

.error-code {
  color: $gray-900;
  font-size: 160px;
  text-align: center;
  line-height: 1;
  font-weight: 600;
  text-shadow: 10px 6px 8px rgba(117, 117, 117, 0.8);

  @media screen and (max-width: 991px) {
    font-size: 130px;
  }

  @media screen and (max-width: 767px) {
    font-size: 100px;
  }

  @media screen and (max-width: 575px) {
    font-size: 80px;
  }
}

.level-title {
  font-size: 100px;

  @media screen and (max-width: 991px) {
    font-size: 80px;
  }

  @media screen and (max-width: 767px) {
    font-size: 60px;
  }

  @media screen and (max-width: 575px) {
    font-size: 40px;
  }
}

.page-error-content h2 {
  margin-bottom: 30px;
  color: $gray-500;

  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
}
